/* About */
.about{
    padding: 100px 10px 50px;
}
.about__title{
    display: inline-block;
    font-size: 22px;
    font-weight: 600;
    line-height: 22px;
    margin-bottom: 20px;
    border-bottom: 4px solid var(--purple);
}
.about__text{
    font-size: 18px;
    font-weight: 400;
    line-height: 25px;
    margin: 10px 0;
}
.about__img{
    display: block;
    width: 95%;
    margin: 50px auto;
    border-radius: 10px;
}
.about__icons{
    width: 100%;
    display: flex;
    margin-top: 20px;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
}
.about__icon{
    margin: 10px;
    height: 30px;
}
@media screen and (min-width:768px) {
    /* About */
    .about{
        display: flex;
        align-items: flex-start;
        justify-content: space-around;
    }
    .about__info{
        width: 40%;
    }
    .about__img{
        display: block;
        width: 45%;
        margin: 0;
    }
}
@media screen and (min-width:1024px) {
    .about__icon{
        height: 50px;
    }
}
/* Footer*/
.footer{
    text-align: center;
    background-color: var(--dark-blue);
    padding: 50px 0;
    color: var(--text-white);
}
.footer__title{
    font-size: 20px;
    font-weight: 600;
    line-height: 20px;
}
.footer__icons{
    margin: 20px 0;
}
.footer__link{
    color: inherit;
    font-size: 30px;
    margin: 0 10px;
}
.footer__link:hover{
    color: var(--purple);
    transition: 0.5s ease-in;
}
.footer__text{
    font-size: 14px;
    font-weight: 300;
    line-height: 14px;
}
@media (max-width:768px) {
    .footer__text{
        font-size: 12px;
        width: 90%;
        margin: 0 auto;
    }
}
/* Header */
.header{
    background: var(--dark-blue);
    color: var(--text-white);
    overflow: hidden;
}
.greeting{    
    display: inline-block;
    padding: 0 10px;
    padding-top: 150px;
}
.greeting__name{
    font-size: 18px;
    line-height: 18px;
    font-weight: 400;
    margin-bottom: 10px;
}
.greeting__title{
    font-size: 36px;
    line-height: 50px;
    font-weight: 700;
    margin-bottom: 40px;
}
.greeting__info{
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    margin-bottom: 20px;
}
.greeting__button{
    display: inline-block;
    padding: 15px 25px;
    border: 1px solid var(--text-white);
    border-radius: 30px;
    color: inherit;
    text-decoration: none;
    cursor: pointer;
}
.greeting__button-icon{
    margin-left: 5px;
}
.greeting__button:hover{
    background-color: var(--text-white);
    color: var(--dark-blue);
    border-color: var(--text-white);
    transition: 0.5s ease;
}
.photo{
    height: 90vh;
    position: relative;
}
.circle{
    position: absolute;
    background-color: var(--circle-color);
    height: var(--circle-radius);
    width: var(--circle-radius);
    border-radius: 50%;
    left: calc(50% - 200px);
    top: 100px;
}
.circle--gradient{
    background: rgb(95,39,205);
    background: linear-gradient(77deg, rgba(95,39,205,1) 37%, rgba(0,0,0,1) 100%);
}
.photo__img{
    position: absolute;
    bottom: 0px;    
    left: calc(50% - 200px);
}
@media screen and (min-width:768px) {
    /* Header */
    .header{
        display: flex;
        justify-content: space-around;
    }
    .greeting{
        width: 40%;
    }
    .photo{
        width: 40%;
        height: 70vh;
    }
}
/* Portfolio */
.portfolio{
    padding: 50px 10px;
}
.portfolio__title{
    display: inline-block;
    font-size: 22px;
    font-weight: 600;
    line-height: 22px;
    border-bottom: 4px solid var(--purple);
}
.cards__container{    
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: 50px 0;
}

@media screen and (min-width:768px) {

    /* Portfolio */
    .portfolio{
        width: 90%;
        margin: 0 auto;
    }
    .cards__container{
        flex-direction: row;
        justify-content: flex-start;
        flex-wrap: wrap;
    }
}
.card{
    background-color: var(--white);
    border-radius: 15px;
    overflow: hidden;
    margin: 20px 10px;
    box-shadow: 16px 9px 44px 3px rgba(0,0,0,0.47);
}
.card__img{
    width: 100%;
    height: 250px;
    object-fit: cover;
    object-position: top;
}
.card:hover .card__img{
    -webkit-transform:  scale(1.05);
            transform:  scale(1.05);
    -webkit-transform-origin: center;
            transform-origin: center;
    transition: 0.5s ease;    
}
.card__info{
    padding: 20px 10px;
}
.card__title{
    font-size: 16px;
    font-weight: 600;
    line-height: 16px;
    margin-bottom: 10px;
}
.card__description{
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
    margin-bottom: 10px;
}
.card__icons{
    margin-top: 20px;
    color: var(--dark-blue);
    display: flex;
    align-items: center;
    justify-content: center;   
}
.card__link{
    font-size: 30px;
    color: inherit;
    text-decoration: none;
    margin: 0 20px;
}
.card__link:hover{
    color: var(--circle-color);
}


@media screen and (min-width:768px) {
    .card{
        width: 47%;
    }
}
@media screen and (min-width:1024px) {    
    .card{
        width: 23%;
    }
}
